import React, { memo, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import UserTopSeller from './UserTopSeller';
import * as selectors from '../../store/selectors';
import { fetchAuthorListAction } from "../../store/actions/thunks";
import isEmpty from "../../validation/isEmpty";

const AuthorList = () => {
    
    const dispatch = useDispatch();
    const authorsState = useSelector(selectors.authorsState);
    var authors = authorsState.data ? authorsState.data : [];
    if(!isEmpty(authors.data)) authors = authors.data;
    
    useEffect(() => {
        dispatch(fetchAuthorListAction());
    }, [dispatch]);

    return (
        <div>
            <ol className="author_list">
            { isEmpty(authors) && authors.length>0 && authors.map((author, index) => (
                <li key={index}>
                    <UserTopSeller user={author} />
                </li>
            ))}
            </ol>
        </div>
    );
};

export default memo(AuthorList);
