//admin_order_management.js
import React, { memo, useEffect  } from "react";
import { useSelector, useDispatch } from 'react-redux';
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import { fetchAuthorListAction } from "../../store/actions/thunks";
import * as selectors from '../../store/selectors';
import isEmpty from "../../validation/isEmpty";
import { navigate } from '@reach/router';
import { experimentalStyled as styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import IconButton from '@mui/material/IconButton';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import api from "../../core/api";

const GlobalStyles = createGlobalStyle`  
  header#myHeader.navbar.sticky.white {
    background: #403f83;
    border-bottom: solid 1px #403f83;
  }  
  header#myHeader.navbar .search #quick_search{
    color: #fff;
    background: rgba(255, 255, 255, .1);
  }
  header#myHeader.navbar.white .btn, .navbar.white a, .navbar.sticky.white a{
    color: #fff;
  }
  header#myHeader .dropdown-toggle::after{
    color: rgba(255, 255, 255, .5);;
  }
  header#myHeader .logo .d-block{
    display: none !important;
  }
  header#myHeader .logo .d-none{
    display: block !important;
  }
  .mainside{
    .connect-wal{
      display: none;
    }
    .logout{
      display: flex;
      align-items: center;
    }
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #fff;
    }
    .item-dropdown .dropdown a{
      color: #fff !important;
    }
  }
`;

const Orders = () => {

  const dispatch = useDispatch();
  const authorsState = useSelector(selectors.authorsState);
  var authors = {};
  if (!isEmpty(authorsState) && !isEmpty(authorsState.data)) authors = authorsState.data ? authorsState.data : {};
  if (!isEmpty(authors.data)) authors = authors.data;
  
  useEffect(() => {
    dispatch(fetchAuthorListAction());
  }, [dispatch]);

  const handleSelectChangeOderBtn = (authorId) =>{    
    navigate("/AuthorForOrder/"+authorId);
  }

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(2),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));



  return (
    <div>
      <GlobalStyles />
      <section className='jumbotron breadcumb no-bg' style={{ backgroundImage: `url(${'./img/background/subheader.jpg'})` }}>
        <div className='mainbreadcumb'>
          <div className='container'>
            <div className='row m-10-hor'>
              <div className='col-12'>
                <h1 className='text-center'>Orders</h1>
              </div>
            </div>
          </div>
        </div>
      </section>
      <div className="container">
        <Box sx={{ flexGrow: 1 }}>
          <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 8, md: 12 }}>
            {
            !isEmpty(authors) &&   
            authors.map((author, index) => (
              <Grid item xs={2} sm={3} md={3} key={index}>
                <Item >                                 
                  <label htmlFor="item_avatar">
                    <IconButton color="primary" aria-label="upload picture" component="span">
                      { 
                        !isEmpty(author) &&  !isEmpty(author.avatar) &&  author.avatar &&                       
                        <img style={{ borderRadius: '50%' }} src={api.baseUrl + author.avatar} id="avatar_img" className="lazy" width="180px" height="180px" alt="" loading="lazy" />
                      }
                    </IconButton>
                  </label> 
                    <Typography gutterBottom variant="h5" component="div">
                    {author.username}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {author.email}
                      {author.wallet}
                    </Typography>
                    <Button size="small" onClick={() => handleSelectChangeOderBtn(`${author._id}`) } >Change oders</Button>
                 
                </Item>
              </Grid>
            ))}
          </Grid>
        </Box>
      </div>

      <Footer />
    </div>
  );
}

export default memo(Orders);
