import React, { memo, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import ColumnNewOrder from '../components/ColumnNewOrder';
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import * as selectors from '../../store/selectors';
import { cancelNFTOrder, fetchAuthorListAction } from "../../store/actions/thunks";
import api from "../../core/api";
import isEmpty from "../../validation/isEmpty";
import {NotificationManager} from 'react-notifications';

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    background: #fff;
  }
  .mainside{
    .connect-wal{
      display: none;
    }
    .logout{
      display: flex;
      align-items: center;
    }
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #111;
    }
    .item-dropdown .dropdown a{
      color: #111 !important;
    }
  }
`;

const AuthorForOrder = ({ authorId = 1}) => {

  const dispatch = useDispatch();
  const authorsState = useSelector(selectors.authorsState);
  var author = authorsState.data ? authorsState.data : {};
  if(!isEmpty(author.data)) author = author.data[0];

  useEffect(() => {
    dispatch(fetchAuthorListAction(authorId));
  }, [dispatch, authorId]);

  const onSelectNft = (nft) => {
    // axois to update the NFT  state
    dispatch(cancelNFTOrder(nft._id));
    NotificationManager.success(`${nft.item_title} canceled.`, "Success");
  }

return (
<div>
<GlobalStyles/>
  { author.banner && 
    <section id='profile_banner' className='jumbotron breadcumb no-bg' style={{backgroundImage: `url(${api.baseUrl + author.banner})`}}>
      <div className='mainbreadcumb'>
      </div>
    </section>
  }

  <section className='container no-bottom'>
    <div className='row'>
      <div className="col-md-12">
         <div className="d_profile de-flex">
              <div className="de-flex-col">
                  <div className="profile_avatar">
                    { author.avatar && 
                      <img src={api.baseUrl + author.avatar} alt=""/>
                    }
                      <i className="fa fa-check"></i>
                      <div className="profile_name">
                          <h4>
                            {author.username}                                          
                              <span className="profile_username">{author.social}</span>
                              <span id="wallet" className="profile_wallet">{author.wallet}</span>
                              <button id="btn_copy" title="Copy Text">Copy</button>
                          </h4>
                      </div>
                  </div>
              </div>
              <div className="profile_follow de-flex">
                  <div className="de-flex-col">
                      <div className="profile_follower">{author.followers} followers</div>
                  </div>
              </div>

          </div>
      </div>
    </div>
  </section>

  <section className='container no-top'>
        
        <div id='zero1' className='onStep fadeIn'>
         <ColumnNewOrder shuffle showLoadMore={true} authorId={authorId} onSelectNft={onSelectNft} />
        </div>
    </section>

  <Footer />
</div>
);
}
export default memo(AuthorForOrder);