import React, { memo, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import ColumnNewRedux from "../components/ColumnNewRedux";
import * as selectors from '../../store/selectors';
import { fetchHotCollections } from "../../store/actions/thunks";
import api from "../../core/api";
import isEmpty from "../../validation/isEmpty";

const GlobalStyles = createGlobalStyle`  
  header#myHeader.navbar .search #quick_search{
    color: #fff;
    background: rgba(255, 255, 255, .1);
  }
  header#myHeader.navbar.white .btn, .navbar.white a, .navbar.sticky.white a{
    color: #fff;
  }
  header#myHeader .dropdown-toggle::after{
    color: rgba(255, 255, 255, .5);;
  }
  header#myHeader .logo .d-block{
    display: none !important;
  }
  header#myHeader .logo .d-none{
    display: block !important;
  }
  .mainside{
    .connect-wal{
      display: none;
    }
    .logout{
      display: flex;
      align-items: center;
    }
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #fff;
    }
    .item-dropdown .dropdown a{
      color: #fff !important;
    }
  }
`;

const Colection = ({ collectionId = 1 }) =>
{
  const [openMenu, setOpenMenu] = React.useState(true);
  const [openMenu1, setOpenMenu1] = React.useState(false);
  const handleBtnClick = () => {
    setOpenMenu(!openMenu);
    setOpenMenu1(false);
    document.getElementById("Mainbtn").classList.add("active");
    document.getElementById("Mainbtn1").classList.remove("active");
  };
  const handleBtnClick1 = () => {
    setOpenMenu1(!openMenu1);
    setOpenMenu(false);
    document.getElementById("Mainbtn1").classList.add("active");
    document.getElementById("Mainbtn").classList.remove("active");
  };

const dispatch = useDispatch();
const hotCollectionsState = useSelector(selectors.hotCollectionsState);
const hotCollections = hotCollectionsState.data ? hotCollectionsState.data[0] : {};

useEffect(() => {
    dispatch(fetchHotCollections(collectionId));
}, [dispatch, collectionId]);

    return (
      <div>
      <GlobalStyles/>        
        { 
          !isEmpty(hotCollections) && !isEmpty(hotCollections.author) &&  isEmpty(hotCollections.author.banner) &&
          <section id='profile_banner' className='jumbotron breadcumb no-bg' style={{backgroundImage: `url(${api.baseUrl + hotCollections.author.banner})`}}>
            <div className='mainbreadcumb'>
            </div>
          </section>
        }
          <section className='jumbotron breadcumb  no-bg' style={{backgroundImage: `url(${'./img/background/subheader.jpg'})`}}>
            <div className='mainbreadcumb'>
              <div className='container'>
                <div className='row m-10-hor'>
                  <div className='col-12'>
                    <h1 className='text-center'>Collectibles</h1>
                  </div>
                </div>
              </div>
            </div>
          </section>
          
        <section className='container d_coll no-top no-bottom'>
          <div className='row'>
            <div className="col-md-12">
              <div className="d_profile">
                <div className="profile_avatar">
                    { !isEmpty(hotCollections) && !isEmpty(hotCollections.author)  &&  hotCollections.author.avatar &&
                      <div className="d_profile_img">
                        <img src={api.baseUrl + hotCollections.author.avatar} alt=""/>
                        <i className="fa fa-check"></i>
                      </div>
                    }
                    <div className="profile_name">
                      <h4>
                          { !isEmpty(hotCollections) && !isEmpty(hotCollections.name) && hotCollections.name }                                                
                          <div className="clearfix"></div>
                          { !isEmpty(hotCollections) && !isEmpty(hotCollections.author) &&  hotCollections.author.wallet &&
                            <span id="wallet" className="profile_wallet">{ hotCollections.author.wallet }</span>
                          }
                          <button id="btn_copy" title="Copy Text">Copy</button>
                      </h4>
                    </div>
                  </div>
                </div>
            </div>
          </div>
        </section>

        <section className='container no-top'>
            <div className='row'>
              <div className='col-lg-12'>
                  <div className="items_filter">
                    <ul className="de_nav">
                        <li id='Mainbtn' className="active"><span onClick={handleBtnClick}>On Sale</span></li>
                        <li id='Mainbtn1' className=""><span onClick={handleBtnClick1}>Owned</span></li>
                    </ul>
                </div>
              </div>
            </div>
            {openMenu && (  
              <div id='zero1' className='onStep fadeIn'>
                { !isEmpty(hotCollections) && !isEmpty(hotCollections.author) && 
                  <ColumnNewRedux shuffle showLoadMore={false} authorId={hotCollections.author ? hotCollections.author.id : 1} /> 
                }
              </div>
            )}
            {openMenu1 && ( 
              <div id='zero2' className='onStep fadeIn'>
                <ColumnNewRedux shuffle showLoadMore={false} />
              </div>
            )}
            </section>
        <Footer />
      </div>
    );
  
}

export default memo(Colection);